:root {
  --blue: #1e90ff;
  --white: #ffffff;
}

*{
  font-family: 'Roboto', sans-serif;
 
}

.my-image>img{
  max-width: 300px;
  height: auto;
  margin-top: 5vh;
}

header,
.title{
  color: rgba(0, 0, 0, .6);
}

.contact>header{
  color: white;
}

.my-image>div{
  color: white;
  margin-top: -50px;
  text-align: center;
  font-size: 1.5rem;
  /* z-index: 1; */
}

.card>img{
  width: 100%;
  height: 200px;
  object-fit: cover;
}
.card>.title{
  font-size: 1.5rem;
  /* text-align: center; */
  padding: 15px;
  display: flex;
  justify-content: space-between;
}

.card>.description{
  padding: 15px;
}

.card{
  display: grid;
  max-width: 300px;
  height: 380px;
  background-color: white;
  box-shadow: 0 5px 5px rgba(0, 0, 0, .3);
  /* display: flex;
  flex-direction: column;
  align-items: center; */
  margin: 5vh 0;
  align-self: center;
}

.card:hover{
  box-shadow: 3px 8px 8px rgba(0,0,0,.4);
}

.profile{
display: flex;
justify-content: center;
margin: 5vw;
margin-top: 0;
/* padding-top: 5vh; */

}

.decoration{
  position: absolute;
  width: 140vw;
  height: 500px;
  background-color: var(--blue);
  z-index: -1;
  clip-path: polygon(0 0, 100% 0, 100% 40%, 0 100%);
}

/* .main{
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
} */
.main-header{
  font-size: 2rem;
  text-align: center;
  /* margin: 0 25vw;
  background-color: white; */

}

svg{
  width: 40px;
  height: auto;
}

.about>header{
  font-size: 2.5rem;
  font-weight: 200;
  color: rgba(0, 0, 0, .6);
  background-color: white;
  text-align: center;
  margin-bottom: 5vh;
}
.about{
  padding: 30px;
  background-color: white;
  font-size: 1.2rem;
  margin-top: 10vh;
  margin-bottom: 5vh;
  box-shadow: 0 3px 5px rgba(0, 0, 0, .7);
}

.about>div{
  background: white;
  line-height: 1.5;
}

.my-image{
  /* padding: 30px;
  background-color: white; */

}

.container{
  margin: 5vw;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 1.5rem
}

.contact{
  background-color: var(--blue);
  color: white;
  padding: 40px;
}

.contact-header{
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

@media screen and (max-width:700px){
  .profile{
    flex-wrap: wrap;
  }
  .decoration{
    clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%);
    height: 170vh;
  }

  .card{
    display: grid;
   justify-self: center;
  }
}